<template>
  <div class="log-viewer">
    <h1>
      系统日志查看
      <div class="header-buttons">
        <el-button type="default" size="small" icon="el-icon-back" @click="goBack">返回管理面板</el-button>
        <el-button type="primary" size="small" icon="el-icon-s-home" @click="goHome">返回主页</el-button>
      </div>
    </h1>

    <el-card class="filter-card">
      <div class="filter-section">
        <el-form :inline="true" :model="filterForm" class="filter-form">
          <el-form-item label="时间范围">
            <el-date-picker
              v-model="filterForm.dateRange"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions"
              @change="handleDateChange">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="用户名">
            <el-input
              v-model="filterForm.username"
              placeholder="请输入用户名"
              clearable
              @clear="handleFilter"
              @keyup.enter.native="handleFilter">
            </el-input>
          </el-form-item>
          <el-form-item label="操作类型">
            <el-select v-model="filterForm.actionType" placeholder="请选择" clearable @change="handleFilter">
              <el-option label="登录" value="login"></el-option>
              <el-option label="登出" value="logout"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" @click="handleFilter">搜索</el-button>
            <el-button icon="el-icon-refresh" @click="resetFilter">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>

    <el-card class="log-table-card">
      <div slot="header" class="card-header">
        <span>操作日志列表</span>
        <div class="header-actions">
          <el-button type="primary" size="small" icon="el-icon-download" @click="exportLogs">导出日志</el-button>
        </div>
      </div>
      
      <el-table
        :data="logData"
        style="width: 100%"
        v-loading="loading"
        border>
        <el-table-column
          prop="timestamp"
          label="时间"
          width="180"
          :formatter="formatDate">
        </el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="action"
          label="操作"
          width="100">
          <template slot-scope="scope">
            <el-tag :type="scope.row.action === 'login' ? 'success' : 'info'">
              {{ scope.row.action === 'login' ? '登录' : '登出' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="ip"
          label="IP地址"
          width="150">
        </el-table-column>
        <el-table-column
          prop="device"
          label="设备信息">
        </el-table-column>
      </el-table>

      <div class="pagination-container">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'LogViewer',
  data() {
    return {
      loading: false,
      filterForm: {
        dateRange: [],
        username: '',
        actionType: ''
      },
      currentPage: 1,
      pageSize: 10,
      total: 0,
      logData: [
        {
          timestamp: '2024-03-20 09:30:00',
          username: 'admin',
          action: 'login',
          ip: '192.168.1.100',
          device: 'Chrome 122.0.0 / Windows 10'
        },
        {
          timestamp: '2024-03-20 12:45:00',
          username: 'user1',
          action: 'login',
          ip: '192.168.1.101',
          device: 'Safari / MacOS'
        },
        {
          timestamp: '2024-03-20 15:20:00',
          username: 'admin',
          action: 'logout',
          ip: '192.168.1.100',
          device: 'Chrome 122.0.0 / Windows 10'
        },
        {
          timestamp: '2024-03-20 17:00:00',
          username: 'user1',
          action: 'logout',
          ip: '192.168.1.101',
          device: 'Safari / MacOS'
        }
      ],
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      }
    }
  },
  methods: {
    goBack() {
      this.$router.push('/admin');
    },
    goHome() {
      this.$router.push('/');
    },
    formatDate(row) {
      return row.timestamp;
    },
    handleFilter() {
      this.currentPage = 1;
      this.fetchLogs();
    },
    resetFilter() {
      this.filterForm = {
        dateRange: [],
        username: '',
        actionType: ''
      };
      this.handleFilter();
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.fetchLogs();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.fetchLogs();
    },
    handleDateChange() {
      this.handleFilter();
    },
    fetchLogs() {
      this.loading = true;
      // 这里应该调用后端API获取日志数据
      // 模拟API调用延迟
      setTimeout(() => {
        this.loading = false;
        // 实际项目中，这里应该使用真实的API数据
        this.total = this.logData.length;
      }, 500);
    },
    exportLogs() {
      // 实现导出日志功能
      this.$message({
        message: '日志导出功能开发中...',
        type: 'info'
      });
    }
  },
  created() {
    this.fetchLogs();
  }
}
</script>

